
:root {
  --key-width: 2.5rem;
  --key-height: 10rem;
}
.key {
  position: relative;
}
.key span {
    display: block;
    width: 10px;
    height: 10px;
    background-color: black;
    bottom: 0.2rem;
    left: 1rem;
    position: absolute;
    border-radius: 50%;
}
.key.white {
    flex: 0 0 var(--key-width);
    background-color: #faf5ed;
    height: var(--key-height);
    box-shadow: inset 0 1px 0px white, inset 0 -1px 0px white, inset 1px 0px 0px white, inset -1px 0px 0px white, 0 4px 3px rgba(0, 0, 0, 0.7), inset 0 -1px 0px white, inset 1px 0px 0px white, inset -1px -1px 7px rgba(0, 0, 0, 0.3), -3px 4px 6px rgba(0, 0, 0, 0.5);
}
.key.white:active {
      background-color: #f4ead9;
}
.key.white + .key.white {
    margin-left: 0.1rem;
}
.key.black {
    flex: 0 0 calc(var(--key-width) / 2);
    background-color: black;
    border: 1px solid black;
    height: calc(var(--key-height) / 2);
    z-index: 10;
    margin: 0 calc((var(--key-width) / 4) * -1);
}
.key.black:active {
      background-color: #262626;
}



